import React, { PureComponent } from 'react';
import Select, { Option } from '../../../common-ui/Select';
import Form from '../../../common-ui/Form';
import InlineList from '../../../common-ui/InlineList';
import { Consumer as Modal } from '../../../common-ui/Modal/context';
import { S_PROFILE_EDIT_MODAL } from '../../../constants/modals';
import PayHistory from '../../student/profile/PayHistory';
import Api from '../../../Api';
import { Button } from 'semantic-ui-react';

import './Profile.css';

export default class StudentProfile extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      name: '',
      grade: -1,
      birthday: '',
      phoneNumber: '',
    };
  }

  componentDidMount() {
    const studentId = window.sessionStorage.getItem('studentId');
    Api.get('student/profile', { params: { studentId } }).then(({ data }) => {
      this.setState({
        name: data.name,
        birthday: data.birthday,
        grade: data.grade,
        phoneNumber: data.phoneNumber,
        isSubscribing:data.isSubscribing
      });
    });
  }
  


  render() {
    const { birthday, name, phoneNumber, grade,isSubscribing } = this.state;

    return (
      <Modal>
        {({ openModal }) => (
          <div className="totalBox"  style={{ display: 'flex', justifyContent: 'center' }}>
            <div style={{ flex: 1, flexDirection: 'row', padding: 3 }}>
              <div 
                style={{
                  
                  flexDirection: 'column',
                  padding: 10,
                  border: '2px rgb(247, 207, 192) solid',
                  borderRadius: 10,
                  alignItems: 'center',
                  minWidth:250
                }}
              >
                <div style={{display:'flex',flexDirection:'row',marginBottom:10}}>
                <div style={{flexmargin:5, fontWeight:'bold',fontSize:20}}>{name} |</div><div> {isSubscribing ? (" "):
                (" ")}</div></div>

                <div style={{textAlign:'center',borderRadius:5, backgroundColor:'rgb(247, 207, 192)'}}> </div>
                <div style={{margin:5}}> : {birthday}</div>
                <div style={{margin:5}}> : {grade}</div>
                <div style={{margin:5}}> : {phoneNumber}</div>

                <Button style={{marginTop : 10}}onPress={() => openModal(S_PROFILE_EDIT_MODAL, { type: 'edit' })}>
                    /
                </Button>
                <Button style={{marginTop : 5}}>{isSubscribing ? ("  "):
                ("  ")}</Button>
                
              </div>
            </div>

            <div className="payHistory"
            >
              <Form.Consumer style={{display:'flex'}}>
                {({ onChange, values }) => (
                  <div style={{display:'flex'}}>
                  <InlineList spacingBetween={1} style={{flex:3 }}>
                    <Select name="PayFilter" onChange={onChange}>
                      <Option label="  " />
                      <Option label=" 30" value="onemonth" />
                      <Option label=" 6" value="sixmonth" />
                    </Select>
                  </InlineList>
                  <Button ></Button>
                  </div>
                )}
              </Form.Consumer>
              <PayHistory />
              
            </div>
          </div>

        )}
      </Modal>
    );
  }
}
